//Global variables for entire site
$global-pages-background: #F0F0F0; //global pages background
$global-padding-for-min-content: 16px;
$global-box-shadow-for-min-content: 0 0 4px 0 #00000014,
  0 2px 4px 0 #0000001f;
$global-border-radius-for-min-content: 3px;
$job-posts-back-ground-hover: #F0F0F0;



//text colors
$dark-blueBlack-text-color-jobPage: #0E1724;
$medium-lightBlack-text-color-jobPage: #4d525b;



//global css classes
@mixin container-95($width: 95%) {
  width: $width;
  margin: 0 auto;
}

//button
@mixin btn($bg-color: $dark-ruddy, $paddingUpAndDown: 5px, $paddingRightAndLeft: 10px) {
  background-color: $bg-color;
  border: none;
  color: white;
  padding: $paddingUpAndDown $paddingRightAndLeft;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}


//Font
@mixin text-style($color: $text-color, $font-size: $font-size-18, $font-wight: $font-wight-400, $font-style: normal, $line-height: 1.55) {
  font-size: $font-size;
  color: $color;
  font-weight: $font-wight;
  font-style: $font-style;
}


//display flex
@mixin display-flex-row-alignCenter($flex-direction: row, $align-items: center) {
  display: flex;
  flex-direction: $flex-direction;
  align-items: $align-items;
}

//hr
@mixin hr ($hr-color: #e2e4e8) {
  width: 100%;
  border: 1.2px solid $hr-color;
  margin: 10px 0;
  display: flex;
  align-items: flex-end;
  background-color: #e2e4e8;
}

//min design for cards in all like aside in jobs page and jobs cards
@mixin min-cards-design-for-all-content($padding: $global-padding-for-min-content) {
  background-color: #fff;
  padding: $padding;
  border-radius: $global-border-radius-for-min-content;
  box-shadow: $global-box-shadow-for-min-content;
  overflow: hidden;
}

@mixin small-search-input($height: 30px) {
  width: 100%;
  height: $height;
  font-size: $font-size-16;
  padding: 3px 5px;
  border: 1px solid $hover-black;
  border-radius: 3px;

  &:hover {
    border: 1px solid $hover-blue;
    border-radius: 3px;
    transition: .3s;
  }

  &:focus-visible {
    outline: 1.5px solid $hover-blue;
  }
}

